.InlinePets_container {
  display: flex;
  flex-direction: row;
  border: 1px solid #E1BB80;
  margin: 10px auto;
  width: 80%;
  /* justify-content: space-between; */
  padding: 5px 10px;
}

.InlinePets_info{
  display: flex;
  flex-direction: column;
  width: 33.33%;
}

.InlinePets_info > .title, 
.InlinePets_info > .content {
  margin-top: 5px;
  margin-bottom: 5px;
  text-align: center;
}

.InlinePets_info > .title {
  opacity: 80%;
}

.InlinePets_info > .content {
  color: #265653;
}

